.alertBox {
	position: absolute;

	width: 30.1%;
	height: 30.1%;
	background-color: #252526;
	
	display: grid;
	grid-template-rows: 21px 1fr 25px;
	z-index: 2;
}

.error {
	color: red;
}

.textBox {
	margin: 5px;
	padding: 5px;
	display: flex;
	justify-content: center;
	align-items: center;
	background-color: #1e1e1e;
	overflow: scroll;
}

.response {
	display: grid;
	grid-auto-columns: 1fr;
	grid-auto-flow: column;
}

button.NO {
	background-color: darkred
}

button.YES {
	background-color: darkgreen;
}

button.NO, button.YES {
	border: 2px #252526 solid;
    color: white;
}