/* Editable Field */

.container {
	position: relative;
	width: 100%;
    height: 100%;
    display: flex;
    flex-direction: row;
}

.editableField {
	width: 100%;
	align-self: start;
	background-color: #1e1e1e;
	box-sizing: border-box;
	border: 2px #252526 solid;
	padding: 5px;
}

.form {
	justify-content: center;
	padding: 0;
	height: 100%;
	width: 100%;
}

.input {
	background-color: #585858;
	box-sizing: border-box;
	border: 2px #252526 solid;
	box-shadow: none;
	color: white;
	text-align: center;
	align-items: center;
	font: 400 13.3333px Arial;
	width: 100%;
	padding: 5px;
}

button.submitHidden {
	display: none;
}

button.any {
	width: 50%;
}

.submit, .increment, .cancel, .infinity, .any {
	display: flex;
	align-self: stretch;
	flex-direction: row;
	justify-content: center;
	background-image: none;
	cursor: pointer;
	box-sizing: border-box;
	border: 2px #252526 solid;
	padding: 0;
	height: 100%;
	font: 400 13.3333px Arial;
    width: 15%;
    height: 100%;
}

.infinity, .any {
	background-color: darkblue;
	color: white;
}

.submit {
	background-color: darkgreen;
	color: white;
}

.submit > img, .increment > img {
    max-height: 15px;
}

.increment {
	background-color: lightgray;
}

.cancel {
	background-color: darkred;
	color: white;
}

/* Single Pairing */

.resultsBridge {
	width: 100%;
	display: grid;
	grid-template-columns: 45px 45px 45px;
	align-content: center;
	align-items: center;
	justify-content: center;
}

.details {
    overflow: hidden;
    transition: height 0.5s ease;
    background-color: rgb(33, 33, 33);
    border-left: rgb(5, 5, 5) 5px solid;
    border-right: rgb(5, 5, 5) 5px solid;
}

.resolve, .reject, .neutral {
	display: flex;
	background-image: none;
	cursor: pointer;
	box-sizing: border-box;
	padding: 0px;
	height: 100%;
	width: 100%;
	color: white;
	text-align: center;
	justify-content: center;
	align-items: center;
}

.neutral {
    transition: transform 0.5s ease;
}

.resolve {
	background-color: darkgreen;
	border: 2px #252526 solid;
	animation: flash-green infinite 1s alternate;
	animation-delay: 5s;
}

@keyframes flash-green {
	from {
		background-color: yellow;
	}
	to {
		background-color: darkgreen;
	}
}

.reject {
	background-color: darkred;
	border: 2px #252526 solid;
}

.reject:focus {
	border: 2px #252526 solid;
}

.setResult {
	padding: 5px;
	background-color: #585858;
	box-sizing: border-box;
	border: 2px #252526 solid;
	white-space: nowrap;
	color: white;
	justify-content: center;
    text-align: center;
    align-items: center;
	background-color: rgb(33, 56, 56);
}

.inputURL {
    text-align: left;
}

.editableField {
    background-color: rgb(44, 44, 44);
    white-space: nowrap;
}

.centre {
	display: inline-block;
	position: relative;
	height: 100%;
	width: 100%;
	vertical-align: middle;
}

.centre:hover span.tooltip {
	opacity: 0.8;
}

.tooltip {
	background-color: black;
	color: white;
	text-align: center;
	padding: 5px;
	border-radius: 6px;
	position: absolute;
	top: -200%;
	width: 120px;
	left: calc(50% - 60px);
	opacity: 0;
	z-index: 1;
	transition: opacity 0.25s;
  }

.tooltip::after {
	content: " ";
	position: absolute;
	top: 100%; /* At the bottom of the tooltip */
	left: 50%;
	margin-left: -15px;
	border-width: 10px;
	border-style: solid;
	border-color: black transparent transparent transparent;
}

.detailsContent {
    width: 100%;
    grid-column: 1 / span 3;
    display: flex;
    flex-direction: column;
    align-content: center;
}

.row {
    width: 100%;
    display: grid;
    grid-template-columns: 85% 15;
    white-space: pre-wrap;
}

.linkSubmit {
    padding: 5px;
    box-sizing: border-box;
}

/* Collapsable Section */

.sectionContainer {
	display: flex;
	flex-direction: column;
	width: 100%;
	overflow: hidden;
}

.sectionHeader {
	z-index: 1;
	display: grid;
	grid-template-columns: auto 29px;
	position: relative;
}

.sectionHeaderText {
	display: flex;
	justify-content: flex-start;
}

.sectionListWrapper {
	overflow: hidden;/*
	transform: scaleY(-1);*/
	transition: height 0.5s ease;
}
/*
.sectionListWrapper > * {
	transform: scaleY(-1);
}*/

.sectionList {
	position: relative;
	top: 0%;
}

.expanded {
	display: initial;
}

.sectionToggle {
	padding: 0 10px;
    cursor: pointer;
	-webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */
     -khtml-user-select: none; /* Konqueror HTML */
       -moz-user-select: none; /* Old versions of Firefox */
        -ms-user-select: none; /* Internet Explorer/Edge */
            user-select: none; /* Non-prefixed version, currently
                                  supported by Chrome, Opera and Firefox */
}

.sectionToggle > * {
    transition: transform 0.25s;
}