div.column {
	height: auto;
}

div.initialiseButton {
	display: grid;
	height: 100%;
	width: 100%;
	min-height: 150px;
	overflow: hidden;
}

div.forgottenContainer {
	display: grid;
	grid-template-columns: repeat(2, 1fr);
	box-sizing: border-box;
	height: 100%;
}

div.forgotten {
	font: 400 12px Arial;
	background-color: #1a1a1a;
	color: grey;
	justify-content: center;
	align-items: center;
}

.form {
	display: grid;
	grid-template-rows: repeat(4, 1fr);
	height: 100%;
}

.form > .input {
	font: 400 12px Arial;
	height: 100%;
	width: 100%;
	overflow: hidden;
}

input.submit {
	height: 100%;
	width: 100%;
	background-color: darkgreen;
	justify-content: center;
}

.loginSection {
	position: relative;
}

.loginBackground {
	display: grid;
	grid-template-columns: repeat(3, 1fr);
	grid-template-rows: 100%;
	align-items: center;
}

.foreground {
	display: flex;
	position: absolute;
	width: 66.6%;
	height: 100%;
	background-color: green;
	opacity: 1;
}

.loginForeground {
	left: 0px;
	-webkit-transition: all 0.68s ease-out;
	-moz-transition: all 0.68s ease-out;
	 -ms-transition: all 0.68s ease-out;
	  -o-transition: all 0.68s ease-out;
		 transition: all 0.68s ease-out;
}

.registerForeground {
	left: 33.3%;
	-webkit-transition: all 0.68s ease-out;
	-moz-transition: all 0.68s ease-out;
	 -ms-transition: all 0.68s ease-out;
	  -o-transition: all 0.68s ease-out;
		 transition: all 0.68s ease-out;
}

div.flip {
	display: flex;
	flex-direction: column;
}

.flip > div {
    margin: 2px 0;
}

div.messageBar {
	display: flex;
	width: 100%;
    padding: 5px 0 5px 5px;
    box-sizing: border-box;
    border: 2px #252526 solid 0.0;
	box-shadow: none;
	justify-content: center;
	color: red;
}