.pairings {
    grid-column: 2 / span 1;
	grid-row: 2 / span 2;
	height: 100%;
	width: 100%;
	display: grid;
	grid-template-rows: 32px 1fr 31px;
	overflow: scroll;
}

.pairingContainer {
    height: 100%;
}

.pairing {
	display: grid;
	grid-template-columns: 3fr 135px 3fr;
	font: 400 13.3333px Arial;
	justify-items: center;
	align-items: center;
	animation: fade-in 1s ease-out;
}

.topBanner {	
	animation: fade-in 1s ease-out;
}

@keyframes fade-in {
	from {
		opacity: 0
	}
	to {
		opacity: 1
	}
}

.playerInfo {
	justify-content: center;
	justify-items: center;
}

div.statusBar {
	display: grid;
    grid-template-columns: 1fr 4fr 1fr;
    grid-column: 2 / span 1;
	position: relative;
	animation: slide-in-from-bottom 1s ease-out;
}

@keyframes slide-in-from-bottom {
	0% {
		bottom: -100%;
	}
	50% {
		bottom: -100%;
	}
	100% {
		bottom: 0%;
	}
}

.waitButton, .finishButton, .nextButton {
    display: flex;
    align-items: center;
}

div.waitButton {
	cursor: default;
}

div.nextButton {
	width: 100%;
	background-color: darkgreen;
	justify-self: center;
}

.nextButton > svg {
    animation: rotate 1s infinite linear;
}

@keyframes rotate {
    0% {
        transform: rotate(0deg)
    }
    50% {
        transform: rotate(180deg)
    }
    100% {
        transform: rotate(360deg)
    }
}

.inputResult {
	padding: 5px 0 5px 5px;
	background-color: #585858;
	box-sizing: border-box;
	border: 2px #252526 solid;
	white-space: nowrap;
	color: white;
	justify-content: center;
    text-align: center;
    align-items: center;
}

.inputResult {
	background-color: #585858;
}

.bye {
	background-color: rgb(36, 36, 36) !important;
}

.submit {
	display: flex;
	align-self: stretch;
	flex-direction: row;
	justify-content: center;
	background-image: none;
	cursor: pointer;
	box-sizing: border-box;
	border: 2px #252526 solid;
	padding: 0px;
	width: 100%;
	background-color: darkgreen;
	color: white;
}

.errorMessage {
	overflow-y: scroll;
	max-height: 31px;
	color: red;
}