@keyframes slide-in-from-right {
	from {
		right: -100%;
	}
	to {
		right: 0%;
	}
}

.profile {
	grid-column: 3 / span 1;
    grid-row: 1 / span 3;

	display: flex;
	flex-direction: column;
	white-space: pre-wrap;
	align-items: flex-start;
	justify-self: stretch;
	align-self: stretch;
	background-color: #252526;
	box-sizing: border-box;
	border: 2px #252526 solid;
    overflow-y: scroll;
    flex-shrink: 0;
    height: 100%;
    width: 100%;
    
	position: relative;
	animation: slide-in-from-right 1s ease-out;
}

.bottomBanner {
    animation: slide-in-from-right 1s ease-out;
    position: relative;
    grid-column: 3 / span 1;
    grid-row: 4 / span 1;
}

div.roundRow {
	grid-template-columns: 30% 70%;
}

.row {
	grid-column: 3 / span 1;
	grid-row: 1 / span 3;
	width: 100%;
	white-space: pre-wrap;
	display: grid;
	grid-template-columns: 38.2% 61.8%;
	align-items: start;
	/*transition: max-height 0.5s ease;*/
}

.resultsRow {
	grid-column: 3 / span 1;
	grid-row: 1 / span 3;
	width: 100%;
	white-space: pre-wrap;
	display: grid;
	grid-template-columns: 10% 70% 10% 10%;
	align-items: start;
}

div.editable, div.editableField {
	background-color: #4b4b4b;
}

div.editableField, input.input {
	font: 400 13.3333px Arial;
	text-align: left;
}

input.input, div.editable:hover, div.editableField:hover {	
	background-color: #585858;
	max-height: 29px;
}

.sectionContainer {
	display: flex;
	flex-direction: column;
	width: 100%;
	overflow: hidden;
}

.sectionHeader {
	z-index: 1;
	display: grid;
	grid-template-columns: auto 29px;
	position: relative;
}

.sectionHeaderText {
	display: flex;
	justify-content: flex-start;
}

.sectionListWrapper {/*
	overflow: hidden;
	transform: scaleY(-1);*/
	transition: height 0.5s ease;
}
/*
.sectionListWrapper > * {
	transform: scaleY(-1);
}*/

.sectionList {
	position: relative;
	top: 0%;
}

.expanded {
	display: initial;
}

.sectionToggle {
	padding: 0 10px;
    cursor: pointer;
	-webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */
     -khtml-user-select: none; /* Konqueror HTML */
       -moz-user-select: none; /* Old versions of Firefox */
        -ms-user-select: none; /* Internet Explorer/Edge */
            user-select: none; /* Non-prefixed version, currently
                                  supported by Chrome, Opera and Firefox */
}

.sectionToggle > * {
    transition: transform 0.25s;
}

div.semiPresent {
	background: repeating-linear-gradient(
		45deg,
		rgb(33, 56, 56),
		rgb(33, 56, 56), 10px,
		rgb(33, 33, 33) 10px,
		rgb(33, 33, 33) 20px
	);
}

div.playerLine {
    grid-template-columns: 85% 15%;
}

input.input {
    width: 85%;
}

button.cancel {
    grid-column: 2 / span 1;
}