div.leftColumn {
	align-items: flex-end;
}

div.rightColumn {
	align-items: flex-start;
}

.line {
	z-index: -1;
}

.row {
	grid-column: 1 / span 2;
	width: 100%;
	background-color: rgb(25, 25, 25);
	grid-template-columns: 75% 25%;
}

.listWrapper {
	overflow: hidden;
	transform: scaleY(-1);
	transition: height 0.5s ease;
	width: 100%;
}

.listWrapper > * {
	transform: scaleY(-1);
}

div.clickButton {
	display: flex;
	box-sizing: border-box;
	width: 100%;
	height: 100%;
	align-self: flex-start;
	align-items: center;
	justify-content: center;
	padding: 5px;
}

div.clickButton:hover {
	background-color: #3c3c3c;
}

.dotContainer {
	display: flex;
	flex-direction: row;
	align-items: center;
}

.dotDescription {
	opacity: 0;
	margin: 0 20px;
	font-size: xx-large;
	font-family: 'Amatic SC', cursive
}

.dot {
	margin: 20px 0;
	background-color: #213838;
	border: 8px solid #252526;
	font: 400 30px Arial;
	-webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

.dot > svg {
    height: 100%;
}

.dotContainerLoaded:hover .dot {
	background-color: #585858;
	border: 8px solid #383838;
}

.dotContainerLoaded:hover .dotDescription, .descriptionSelected {
	opacity: 1;
}

div.close:hover {
	background-color: darkred;
}


.sidebar {
    display: grid;
    grid-template-rows: auto 29px;
    grid-auto-flow: row;
    height: 100%;
    width: 100%;
}

.profile {
    grid-column: unset;
    grid-row: unset;
}

.bottomBanner {
    animation: slide-in-from-right 1s ease-out;
    position: relative;
}

@keyframes slide-in-from-right {
	from {
		right: -100%;
	}
	to {
		right: 0%;
	}
}

@media screen {
	.logo {
        animation: float-down 0.5s ease-out;
	}
	.dotUnloaded {
		animation: expand 3s ease-out;
	}
}

@keyframes float-down {
	from {
		top: -80px;
	}
	to {
		top: 0px;
	}
}

@keyframes expand {
	0% {
		transform: scale(0)
	}
	75% {
		transform: scale(0)
	}
	100% {
		transform: scale(1)
	}
}

@keyframes briefly-expand {
	from {
		transform: scale(1)
	}	
	to {
		transform: scale(2)
	}
}